import { Component, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { ValueAccessor } from '@helix/platform/shared/components';
import { IFormControlComponent, IFormFocusable } from '@helix/platform/shared/api';
import { noop } from 'lodash';
import { RxShellGlobalSearchRecordsModalComponent } from './shell-global-search-records-modal/shell-global-search-records-modal.component';
import { IShellGlobalSearchRecordsModalConfig } from './shell-global-search-records-modal/shell-global-search-records-modal.types';
import { IShellGlobalSearchRecord } from '../../shell-design.types';
import { OpenViewActionModalSize } from '@helix/platform/view/api';
import { RxModalService } from '@helix/platform/ui-kit';

@Component({
  selector: 'rx-shell-global-search-records-control',
  templateUrl: './shell-global-search-records-control.component.html',
  styleUrls: ['./shell-global-search-records-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RxShellGlobalSearchRecordsControlComponent,
      multi: true
    }
  ]
})
export class RxShellGlobalSearchRecordsControlComponent
  extends ValueAccessor<IShellGlobalSearchRecord[]>
  implements IFormControlComponent, IFormFocusable
{
  @Input()
  options;

  constructor(private rxModalService: RxModalService) {
    super();
  }

  openModal(recordToEdit?: IShellGlobalSearchRecord): void {
    this.rxModalService
      .openModal({
        title: 'Configure results view',
        data: {
          selectedRecords: this.value,
          recordToEdit,
          isReadOnly: this.isDisabled
        } as IShellGlobalSearchRecordsModalConfig,
        content: RxShellGlobalSearchRecordsModalComponent,
        size: OpenViewActionModalSize.Large as any,
        testID: 'configure-results-view'
      })
      .then((records) => {
        this.value = records;
      })
      .catch(noop);
  }

  focus(): void {
    this.openModal();
  }

  edit(record: IShellGlobalSearchRecord): void {
    this.openModal(record);
  }

  remove(record: IShellGlobalSearchRecord): void {
    this.value = this.value.filter((item) => item !== record);
  }
}
