import { Component, Injector, Input } from '@angular/core';
import { IShellMenuItemsWidgetConfig } from './shell-menu-items-control.types';
import { RX_SHELL } from '@helix/platform/view/api';
import { IShellMenuItem } from '../../shell-design.types';
import { InspectorWidgetBase } from '@helix/platform/shared/components';
import { IFormWidgetComponent, IPlainObject } from '@helix/platform/shared/api';
import { RxShellDesignModel } from '../../shell-design.model';
import { RxUtilityModalsService } from '@helix/platform/ui-kit';

@Component({
  selector: 'rx-shell-menu-items-control',
  templateUrl: './shell-menu-items-control.component.html'
})
export class RxShellMenuItemsControlComponent
  extends InspectorWidgetBase<IPlainObject, RxShellDesignModel>
  implements IFormWidgetComponent {
  @Input()
  options: IShellMenuItemsWidgetConfig;

  @Input()
  isDisabled: boolean;

  menuGroupLabel = 'Menu group';

  menuItemTypeToLabelMap = {
    [RX_SHELL.navBar.menuItem]: 'Menu item',
    [RX_SHELL.navBar.menuGroup]: this.menuGroupLabel,
    [RX_SHELL.navBar.userMenu]: this.menuGroupLabel
  };

  actionNameToLabelMap = {
    [RX_SHELL.actions.launchURL]: 'Launch URL',
    [RX_SHELL.actions.navigateToView]: 'Navigate to view',
    [RX_SHELL.actions.navigateToSmartReporting]: 'Navigate to Smart Reporting'
  };

  constructor(protected injector: Injector, private rxUtilityModalsService: RxUtilityModalsService) {
    super(injector);
  }

  edit(menuItem: IShellMenuItem): void {
    this.designerItemModel.selectMenuItem(menuItem.guid);
  }

  remove(menuItem: IShellMenuItem): void {
    this.rxUtilityModalsService
      .confirm('Are you sure you want to delete this menu item?')
      .then((isConfirmed: boolean) => {
        if (isConfirmed) {
          this.designerItemModel.removeMenuItem(menuItem.guid);
        }
      });
  }

  canBeRemoved(menuItem: IShellMenuItem): boolean {
    return menuItem.type !== RX_SHELL.navBar.userMenu && !this.isDisabled;
  }

  trackByGuid(index: number, menuItem: IShellMenuItem): string {
    return menuItem.guid;
  }
}
