import {
  AfterViewInit,
  Component,
  ElementRef,
  Injector,
  OnInit,
  QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActiveModalRef, AdaptAccordionTabComponent, DismissReasons, TreeWrap } from '@bmc-ux/adapt-angular';
import {
  IShellGlobalSearchRecordItem,
  IShellGlobalSearchRecordsModalConfig,
  IShellGlobalSearchSelectedItem
} from './shell-global-search-records-modal.types';
import { RxRecordDefinitionDataPageService } from '@helix/platform/record/api';
import { IDataPageResult, RxDefinitionNameService } from '@helix/platform/shared/api';
import { CdkDragDrop } from '@angular/cdk/drag-drop/drag-events';
import { IDefinitionPickerComponentOptions, RxDefinitionPickerType } from '@helix/platform/shared/components';
import { IShellGlobalSearchRecord } from '../../../shell-design.types';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { TreeNode } from 'primeng/api/treenode';
import { RxModalClass } from '@helix/platform/ui-kit';
import { findIndex } from 'lodash';

@Component({
  selector: 'rx-shell-global-search-records-modal',
  templateUrl: './shell-global-search-records-modal.component.html',
  styleUrls: ['./shell-global-search-records-modal.component.scss']
})
export class RxShellGlobalSearchRecordsModalComponent extends RxModalClass implements OnInit, AfterViewInit {
  @ViewChildren(AdaptAccordionTabComponent, { read: ElementRef })
  private accordionTabEls: QueryList<ElementRef>;

  @ViewChild('selectedRecordsForm', { read: NgForm, static: true })
  selectedRecordsForm: NgForm;

  config: IShellGlobalSearchRecordsModalConfig = this.activeModalRef.getData();
  availableRecordDefinitionItems: IShellGlobalSearchRecordItem[];
  filteredRecordDefinitionItems: TreeNode<IShellGlobalSearchRecordItem>[] = [];
  selectedRecords: IShellGlobalSearchSelectedItem[];
  treeWrap: TreeWrap = TreeWrap.WrapAll;
  DismissReasons = DismissReasons;

  definitionPickerOptions: IDefinitionPickerComponentOptions = {
    label: 'Display view when clicked',
    definitionType: RxDefinitionPickerType.View,
    required: true
  };

  constructor(
    public activeModalRef: ActiveModalRef,
    private recordDefinitionDataPageService: RxRecordDefinitionDataPageService,
    private definitionNameService: RxDefinitionNameService,
    protected injector: Injector
  ) {
    super(activeModalRef, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.selectedRecords = this.config.selectedRecords.map((record) => ({
      ...record,
      isOpen: record.name === this.config.recordToEdit?.name
    }));

    this.getRecordDefinitions().subscribe((records) => {
      this.availableRecordDefinitionItems = records;
      this.updateFilteredItems();
    });
  }

  updateFilteredItems(): void {
    this.filteredRecordDefinitionItems = this.availableRecordDefinitionItems
      .filter((item) => !this.selectedRecords.find((record) => record.name === item.name))
      .map((item) => ({
        data: item,
        label: item.definitionName
      }));
  }

  addRecord(recordItem: IShellGlobalSearchRecordItem): void {
    this.markAsDirty();
    this.selectedRecords.push({ ...recordItem, isOpen: true, view: null });
    this.updateFilteredItems();

    setTimeout(() => {
      this.accordionTabEls.last.nativeElement.scrollIntoView({
        block: 'nearest'
      });
    });
  }

  toggleOpen(expandAll: boolean): void {
    this.selectedRecords.forEach((record) => (record.isOpen = expandAll));
  }

  onSelectedRecordsListDrop(event: CdkDragDrop<IShellGlobalSearchRecordItem>): void {
    this.addRecord(event.item.data);
  }

  removeRecord(record: IShellGlobalSearchSelectedItem): void {
    this.markAsDirty();

    this.selectedRecords = this.selectedRecords.filter((item) => item.name !== record.name);

    this.updateFilteredItems();
  }

  onSave(): void {
    const result: IShellGlobalSearchRecord[] = this.selectedRecords.map((record) => ({
      name: record.name,
      view: record.view,
      definitionName: record.definitionName
    }));

    this.activeModalRef.close(result);
  }

  private getRecordDefinitions(): Observable<IShellGlobalSearchRecordItem[]> {
    return this.recordDefinitionDataPageService
      .get({
        params: {
          propertySelection: 'name',
          excludeCustomRecordDefinitions: true
        }
      })
      .pipe(
        map((result: IDataPageResult) =>
          result.data.map((record) => ({
            name: record.name,
            definitionName: this.definitionNameService.getDisplayName(record.name)
          }))
        )
      );
  }

  ngAfterViewInit(): void {
    const selectedRecordIndex = findIndex(this.selectedRecords, 'isOpen');

    if (selectedRecordIndex !== -1) {
      this.accordionTabEls.toArray()[selectedRecordIndex].nativeElement.scrollIntoView({
        block: 'nearest'
      });
    }
  }
}
