import { RxShellMenuItemDesignModel } from '../shell-menu-item/shell-menu-item-design.model';
import { map } from 'rxjs/operators';
import { IRxShellMenuItemProperties } from '../shell-menu-item/shell-menu-item-design.types';
import { IViewComponentDesignValidationIssue } from '@helix/platform/view/designer';

export class RxShellActionDesignModel extends RxShellMenuItemDesignModel {
  iconClass$ = this.menuItemIcon$.pipe(map((className) => (className ? `d-icon-${className}` : '')));
  menuItemNameLabel = 'Tooltip';

  static getInitialProperties(initialProperties?: IRxShellMenuItemProperties): IRxShellMenuItemProperties {
    const result = super.getInitialProperties({
      ...initialProperties,
      menuItemName: initialProperties?.menuItemName ?? 'New action'
    });

    result.menuItemIcon = result.menuItemIcon || 'triangle_right_circle_o';

    return result;
  }

  validate(props: IRxShellMenuItemProperties): IViewComponentDesignValidationIssue[] {
    const validationIssues = super.validate(props);

    if (!props.menuItemIcon) {
      validationIssues.push(this.sandbox.createError(`Icon cannot be blank.`, 'menuItemIcon'));
    }

    return validationIssues;
  }
}
