import { RX_PERMISSION, Tooltip } from '@helix/platform/shared/api';
import { IViewDefinitionPermission, IViewDesignerComponentModel } from '@helix/platform/view/api';
import {
  IComponentPermissionEditorConfig,
  IViewComponentDesignValidationIssue,
  IViewDesignerInspectorConfig,
  RxComponentPermissionEditorWidgetComponent,
  validateCssClassName,
  validateCssClassNames,
  ViewDesignerComponentModel
} from '@helix/platform/view/designer';
import {
  ITagsFormControlOptions,
  SwitchFormControlComponent,
  TagsFormControlComponent,
  TextFormControlComponent
} from '@helix/platform/shared/components';
import { IRxShellMenuGroupProps } from './shell-menu-group-design.types';
import { trim } from 'lodash';
import { pluck } from 'rxjs/operators';
import { Observable } from 'rxjs';

export class RxShellMenuGroupDesignModel
  extends ViewDesignerComponentModel<IRxShellMenuGroupProps>
  implements IViewDesignerComponentModel<IRxShellMenuGroupProps> {
  componentProperties$: Observable<IRxShellMenuGroupProps> = this.sandbox.componentProperties$;
  label$ = this.componentProperties$.pipe(pluck('menuGroupName'));

  static getInitialProperties(initialProperties?: IRxShellMenuGroupProps): IRxShellMenuGroupProps {
    return {
      menuGroupName: 'Menu group',
      hidden: false,
      styles: null,
      ...initialProperties
    };
  }

  static getDefaultPermissions(): IViewDefinitionPermission[] {
    const defaultPermissions: IViewDefinitionPermission[] = [
      {
        ownerId: RX_PERMISSION.permissionDialogMetadata.viewComponent.defaultPermission,
        type: RX_PERMISSION.permissionDialogMetadata.viewComponent.defaultPermittedAction
      } as IViewDefinitionPermission
    ];

    return defaultPermissions;
  }

  rxInit(): void {
    this.sandbox.updateInspectorConfig(this.getInspector());

    this.componentProperties$.subscribe((props: IRxShellMenuGroupProps) => {
      this.sandbox.setValidationIssues(this.validate(props));
    });

    this.sandbox.getComponentPropertyValue('menuGroupName').subscribe((menuGroupName) => {
      this.sandbox.setBreadcrumbs(menuGroupName);
    });
  }

  private validate(props: IRxShellMenuGroupProps): IViewComponentDesignValidationIssue[] {
    const validationIssues: IViewComponentDesignValidationIssue[] = [];

    if (!trim(props.menuGroupName)) {
      validationIssues.push(this.sandbox.createError('Label cannot be blank.', 'menuGroupName'));
    }

    validationIssues.push(...validateCssClassNames(props.styles));

    return validationIssues;
  }

  private getInspector(): IViewDesignerInspectorConfig {
    return {
      inspectorSectionConfigs: [
        {
          label: 'Properties',
          controls: [
            {
              name: 'menuGroupName',
              component: TextFormControlComponent,
              options: {
                label: 'Label',
                required: true
              }
            },
            {
              name: 'hidden',
              component: SwitchFormControlComponent,
              options: {
                label: 'Hidden'
              }
            },
            {
              name: 'styles',
              component: TagsFormControlComponent,
              options: {
                label: 'CSS classes',
                placeholder: 'Add CSS classes',
                tooltip: new Tooltip('Enter CSS class names to apply to this view component.'),
                errorCheck: validateCssClassName
              } as ITagsFormControlOptions
            },
            {
              component: RxComponentPermissionEditorWidgetComponent,
              options: {
                label: 'Permissions',
                type: 'view',
                componentGuid: this.sandbox.guid
              } as IComponentPermissionEditorConfig
            }
          ]
        }
      ]
    };
  }
}
