import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from '@helix/platform/shared/api';
import {
  IContainerViewComponentDefinition,
  IViewDefinition,
  IViewLayout,
  RX_SHELL,
  RX_VIEW_DEFINITION,
  RxViewLayout
} from '@helix/platform/view/api';
import { RxGuidService } from '@helix/platform/utils';

@Injectable()
export class RxShellDesignAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition, IViewDefinition> {
  constructor(private rxGuidService: RxGuidService) {}

  adaptDefinition(componentDefinition: IContainerViewComponentDefinition): void {
    this.addAddUserMenu(componentDefinition);
    this.addActionsOutlet(componentDefinition);
  }

  addAddUserMenu(componentDefinition: IContainerViewComponentDefinition): void {
    const userMenuGuid = this.rxGuidService.generate();

    const userMenuDefinition = {
      resourceType: RX_VIEW_DEFINITION.resourceTypes.containerViewComponent,
      guid: userMenuGuid,
      type: RX_SHELL.navBar.userMenu,
      layout: JSON.stringify(RxViewLayout.getViewLayoutForDefaultOutlet()),
      componentDefinitions: [],
      propertiesByName: {
        menuGroupName: 'User menu'
      }
    };

    const userMenu = componentDefinition.componentDefinitions.find(
      (component) => component.type === RX_SHELL.navBar.userMenu
    );

    if (!userMenu) {
      const layout: IViewLayout = JSON.parse(componentDefinition.layout);

      layout.outlets[0].columns[0].children.push(userMenuGuid);
      componentDefinition.layout = JSON.stringify(layout);

      componentDefinition.componentDefinitions.push(userMenuDefinition);
    }
  }

  addActionsOutlet(componentDefinition: IContainerViewComponentDefinition): void {
    const layout: IViewLayout = JSON.parse(componentDefinition.layout);

    if (!layout.outlets.find((outlet) => outlet.name === RX_SHELL.outlets.actions)) {
      layout.outlets.push(RxViewLayout.getOutlet(RX_SHELL.outlets.actions));
      componentDefinition.layout = JSON.stringify(layout);
    }
  }
}
